import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import * as actions from './redux/actions';
import { Alert, Icon } from 'fundamental-react';
// import { Link } from 'react-router-dom';
import eureka from 'eureka';
import {
  Typography,
  // Grid,
  // IconButton,
  // TextField,
  // Tooltip,
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
} from '@material-ui/core';
import ReuseIcon from '@material-ui/icons/Settings';
import BackendIcon from '@material-ui/icons/Widgets';
import FrontendIcon from '@material-ui/icons/Extension';
import CanvasIcon from '@material-ui/icons/Layers';
import FunctionIcon from '@material-ui/icons/Functions';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import ReuseServiceDialog from './components/ReuseServiceDialog';
// import Sample from './components/Sample';
import AddServiceDialog from './components/AddServiceDialog';
import AddFrontendDialog from './components/AddFrontendDialog';

const { Spinner } = eureka.components;

// Using native HTML Drag and Drop API
// https://developer.mozilla.org/en-US/docs/Web/API/HTML_Drag_and_Drop_API
export class CanvasView extends Component {
  static propTypes = {
    businessPartner: PropTypes.object.isRequired,
    actions: PropTypes.object.isRequired,
  };

  constructor(props) {
    super(props);
  }

  state = {
    disabled: false,
    frontends: [
      // { index: 1, name: 'Shell UI', type: 'reuse' },
      // { index: 2, name: 'Login UI', type: 'reuse' },
      // { index: 3, name: 'Claims UI', type: 'normal' },
    ],
    backends: [
      // { index: 1, name: 'ICM Previsoning Serivice', type: 'normal' },
      // { index: 2, name: 'Claims Service', type: 'reuse' },
      // { index: 3, name: 'Glory Service', type: 'reuse' },
    ],
    feDragStarted: false,
    beDragStarted: false,
    feHighlight: false,
    beHighlight: false,
    hasBff: false,
    dialogOpen: false,
    addFeDialogOpen: false,
    addBeDialogOpen: false,
    addReuseSvcDialogOpen: false,
  };

  frontendConfig = {
    organization: {
      value: '',
      in: ['Eureka', 'xboard'],
    },
    repository: {
      value: '',
    },
    language: {
      value: '',
      in: [
        {
          name: 'React',
          description: 'A JavaScript library for building user interfaces.',
        },
        {
          name: 'Vue',
          description: 'The Progressive JavaScript Framework.',
        },
        {
          name: 'Angular',
          description: 'One framework. Mobile & desktop.',
        },
      ],
    },
    capaiblities: [],
  };

  serviceConfig = {
    organization: {
      value: '',
      in: ['Eureka', 'xboard'],
    },
    repository: {
      value: '',
    },
    language: {
      value: '',
      in: [
        {
          name: 'Java',
          description: 'A backend microservice written in Java and Springboot',
        },
        {
          name: 'Go',
          description: 'A backend microservice written in GoLang and Gin framework',
        },
        {
          name: 'TypeScript',
          description: 'A backend microservice written in TypeScript and Express framework',
        },
        {
          name: 'Python',
          description: 'A backend microservice written in Python and Django framework',
        },
      ],
    },
    capaiblities: [
      {
        title: 'Localization',
        inputs: [
          {
            label: 'Language',
            field: 'Language',
            value: '',
            defaultValue: '',
            in: ['Slim Exporter', 'Multi-String Store'],
          },
        ],
      },
      {
        title: 'Observation',
        inputs: [
          {
            label: 'Database type',
            field: 'Database-type',
            value: '',
            defaultValue: 'HANA',
            in: ['Sentry', 'Metering', 'Actuator', 'APM'],
          },
        ],
      },
      {
        title: 'Context',
        inputs: [
          {
            label: 'Storage service',
            field: 'Storage-service',
            value: '',
            defaultValue: 'Google Storage',
            in: ['Web Context'],
          },
        ],
      },
      {
        title: 'Database',
        inputs: [
          {
            label: 'Cache service',
            field: 'Cache-service',
            value: '',
            defaultValue: 'Redis',
            in: ['SAP HANA', 'PostgreSQL'],
          },
        ],
      },
      {
        title: 'DB Multi-Tenancy',
        inputs: [
          {
            label: 'Messaging service',
            field: 'Messaging-service',
            value: '',
            defaultValue: 'Kafka',
            in: ['Column Based', 'Schema Based'],
          },
        ],
      },
      {
        title: 'Cache Multi-Tenancy',
        inputs: [
          {
            label: 'Messaging service',
            field: 'Messaging-service',
            value: '',
            defaultValue: 'Kafka',
            in: ['Redis Prefix Based', 'Redis Database Based'],
          },
        ],
      },
      {
        title: 'Eventing',
        inputs: [
          {
            label: 'Messaging service',
            field: 'Messaging-service',
            value: '',
            defaultValue: 'Kafka',
            in: ['Reliable Pub', 'Idempotent Sub'],
          },
        ],
      },
      {
        title: 'Security',
        inputs: [
          {
            label: 'Messaging service',
            field: 'Messaging-service',
            value: '',
            defaultValue: 'Kafka',
            in: ['JWT Support', 'Vault Support'],
          },
        ],
      },
      {
        title: 'Rest API',
        inputs: [
          {
            label: 'Messaging service',
            field: 'Messaging-service',
            value: '',
            defaultValue: 'Kafka',
            in: ['Swagger UI'],
          },
        ],
      },
    ],
  };

  onClickSetup = args => {
    this.setState(state => ({
      backends: state.backends.concat({
        index: this.state.backends.length + 1,
        name: args.reuseService,
        type: 'reuse',
      }),
      addReuseSvcDialogOpen: false,
    }));
  };

  reuseServices = [
    {
      name: 'Tenant LCM',
      description:
        'A readymade tenant lifecycle management service which help you to orchestrate tenant lifecycle related scenarios (e.g. tenant provisioning, tenant clone, tenant deletion) in an event based approach. For API definition, please refer to https://docs.eurekacloud.io.',
      onClickSetup: this.onClickSetup,
    },
    {
      name: 'Notification',
      description:
        'A robust push notification service which help you to share information between server and frontend via Websocket connections. You are free to use the service to send notification to specific client, user or tenant with dynamic configuration capabilities it provides.',
      onClickSetup: this.onClickSetup,
    },
    {
      name: 'Email Manager',
      description:
        'A reuse service which help you to manage email templates as rich text html pages, and send emails with Eureka provided infrastructure. For configuration definition, please refer to https://docs.eurekacloud.io.',
      onClickSetup: this.onClickSetup,
    },
    {
      name: 'SAP IAS',
      description:
        'A SAP Cloud Platform kernel service which serves as a Identity Server Provider which handles all user authentication issues. Here we provide an easy configuration and integration to help you easily utilize this kernel service at ease.',
      onClickSetup: this.onClickSetup,
    },
  ];

  componentDidMount() {
    console.log('Canvas view initialized');
  }

  onFrontendDragStart = evt => {
    if (this.state.disabled || this.state.beDragStarted) {
      return;
    }

    console.log('onFrontendDragStart');
    this.setState({
      feDragStarted: true,
      feHighlight: false,
      beDragStarted: false,
      beHighlight: false,
    });
  };

  onFrontendDragOver = evt => {
    if (this.state.disabled || this.state.beDragStarted || this.state.beHighlight) {
      return;
    }

    evt.preventDefault();
    evt.stopPropagation();
    this.setState({
      feHighlight: true,
      // feDragStarted: false,
      beDragStarted: false,
      beHighlight: false,
    });
  };

  onFrontendDragLeave = evt => {
    if (this.state.beDragStarted) {
      return;
    }

    evt.preventDefault();
    evt.stopPropagation();
    this.setState({
      feHighlight: false,
      // feDragStarted: false,
      beDragStarted: false,
      beHighlight: false,
    });
  };

  onFrontendDrop = evt => {
    if (this.state.beDragStarted) {
      return;
    }

    evt.preventDefault();
    evt.stopPropagation();
    const frontends = this.state.frontends;
    if (frontends.length === 0) {
      this.setState({
        dialogOpen: true,
      });
      frontends.push({ index: 1, name: 'Shell UI', type: 'reuse' });
      frontends.push({ index: 2, name: 'Login UI', type: 'reuse' });
      // frontends.push({ index: 3, name: 'Unnamed MicroFrontend UI 1', type: 'normal' });
    } else {
      this.setState({
        addFeDialogOpen: true,
      });
    }
    this.setState({
      frontends,
      feHighlight: false,
      feDragStarted: false,
      beDragStarted: false,
      beHighlight: false,
      hasBff: true,
    });
  };

  onFrontendDragEnd = evt => {
    if (this.state.disabled) {
      return;
    }

    console.log('onFrontendDragEnd');
    evt.preventDefault();
    evt.stopPropagation();
    this.setState({
      feHighlight: false,
      feDragStarted: false,
      beDragStarted: false,
      beHighlight: false,
    });
  };

  onBackendDragStart = evt => {
    if (this.state.disabled) {
      return;
    }

    const targetId = evt.target.id;
    // evt.dataTransfer.dropEffect = targetId;
    console.log('onBackendDragStart', targetId);
    evt.dataTransfer.setData('targetId', targetId);
    this.setState({
      feDragStarted: false,
      feHighlight: false,
      beDragStarted: true,
      beHighlight: false,
    });
  };

  onBackendDragOver = evt => {
    if (this.state.disabled || this.state.feDragStarted || this.state.feHighlight) {
      return;
    }

    evt.preventDefault();
    evt.stopPropagation();
    this.setState({
      feHighlight: false,
      feDragStarted: false,
      // beDragStarted: false,
      beHighlight: true,
    });
  };

  onBackendDragLeave = evt => {
    if (this.state.feDragStarted) {
      return;
    }

    evt.preventDefault();
    evt.stopPropagation();
    this.setState({
      feHighlight: false,
      feDragStarted: false,
      // beDragStarted: false,
      beHighlight: false,
    });
  };

  onBackendDrop = evt => {
    if (this.state.feDragStarted) {
      return;
    }

    const targetSvc = evt.dataTransfer.getData('targetId');
    console.log(`onBackendDrop`, targetSvc);

    evt.preventDefault();
    evt.stopPropagation();

    if (targetSvc === 'backend-btn') {
      this.setState({
        addBeDialogOpen: true,
      });
    } else {
      this.setState({
        addReuseSvcDialogOpen: true,
      });
    }
    this.setState({
      feHighlight: false,
      feDragStarted: false,
      beDragStarted: false,
      beHighlight: false,
    });
  };

  onBackendDragEnd = evt => {
    if (this.state.disabled) {
      return;
    }

    evt.preventDefault();
    evt.stopPropagation();
    this.setState({
      feDragStarted: false,
      feHighlight: false,
      beDragStarted: false,
      beHighlight: false,
    });
  };

  handleDialogClose = evt => {
    evt.preventDefault();
    evt.stopPropagation();
    this.setState({
      dialogOpen: false,
    });

    // input repo name
    this.setState({
      addFeDialogOpen: true,
    });
  };

  renderInitializing() {
    return (
      <div className="businesspartner-list-view list-view">
        <Spinner cssClass="page-load-spinner" />
      </div>
    );
  }

  renderError(err) {
    return (
      <Alert style={{ margin: '10px' }} dismissible type="error">
        <Icon glyph="message-error" />
        Failed to load, please try again. &nbsp;&nbsp;
        <button className="fd-link" onClick={null}>
          Learn More
        </button>
      </Alert>
    );
  }

  render() {
    return (
      <>
        <Typography id="canvas-title" color="primary" variant="h4">
          Architecture Canvas
        </Typography>
        <section className="main-canvas">
          <div className="cols-2-wrapper">
            <div className="arch-canvas">
              {/* <h4 className="sub-title">SCP Gardener</h4>　 */}
              <div
                className={`frontend-area dropzone ${
                  this.state.feDragStarted ? 'is-started' : ''
                } ${this.state.feHighlight ? 'is-active' : ''} ${'fe-' +
                  this.state.frontends.length}`}
                onDragOver={this.onFrontendDragOver}
                onDragLeave={this.onFrontendDragLeave}
                onDrop={this.onFrontendDrop}
              >
                <h5>Frontend</h5>
                {this.state.frontends.map(fe => {
                  return (
                    <button
                      key={`fe-tile-${fe.index}`}
                      data-id={`${fe.index}`}
                      className={`svc-tile fe-tile ${fe.type}`}
                      // draggable="true"
                    >
                      {fe.name}
                    </button>
                  );
                })}
              </div>

              {this.state.hasBff && this.state.frontends.length < 4 && (
                <div className={`fe-lines ${'fe-lines-' + this.state.frontends.length}`}>
                  {this.state.frontends.map((fe, idx) => {
                    return <div className={`line fe-line-${idx + 1}`}></div>;
                  })}
                </div>
              )}

              <Dialog
                open={this.state.dialogOpen}
                onClose={this.handleDialogClose}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
              >
                <DialogTitle id="alert-dialog-title">{'Information'}</DialogTitle>
                <DialogContent>
                  <DialogContentText id="alert-dialog-description">
                    Frontend service requires a “Backend for Frontend” service to serve as bridge
                    between frontend and backend services, which is automatically added along with a
                    standard Shell UI and Login UI.
                  </DialogContentText>
                </DialogContent>
                <DialogActions>
                  <Button onClick={this.handleDialogClose} color="primary" autoFocus>
                    OK
                  </Button>
                </DialogActions>
              </Dialog>

              {this.state.hasBff && (
                <div
                  className={`bff-area ${'bff-fe-' + this.state.frontends.length} ${'bff-be-' +
                    this.state.backends.length}`}
                >
                  {this.state.frontends.length < 4 &&
                    this.state.frontends.map((fe, idx) => {
                      return (
                        <i
                          class={`arrow fe-arrow fe-arrow-${idx + 1} ${
                            this.state.frontends.length < 3 ? 'hide' : ''
                          } down`}
                        ></i>
                      );
                    })}
                  <div className={`bff-tile`}>Backend for Frontend</div>
                </div>
              )}

              {this.state.hasBff && this.state.backends.length < 4 && (
                <div className={`be-lines ${'be-lines-' + this.state.backends.length}`}>
                  {this.state.backends.map((be, idx) => {
                    return <div className={`line be-line-${idx + 1}`}></div>;
                  })}
                </div>
              )}

              <div
                className={`backend-area dropzone ${this.state.beHighlight ? 'is-active' : ''} ${
                  this.state.beDragStarted ? 'is-started' : ''
                } ${'be-' + this.state.backends.length}
            `}
                onDragOver={this.onBackendDragOver}
                onDragLeave={this.onBackendDragLeave}
                onDrop={this.onBackendDrop}
              >
                <h5>Backend</h5>
                {this.state.backends.map((be, idx) => {
                  return (
                    <div className="tile-wrapper be-tile-wrapper">
                      <i
                        class={`arrow be-arrow be-arrow-${idx + 1} down ${
                          !this.state.hasBff || this.state.backends.length > 3 ? 'hide' : ''
                        }`}
                      ></i>
                      <button
                        key={`be-tile-${be.index}`}
                        data-id={`${be.index}`}
                        className={`svc-tile be-tile ${be.type}`}
                        // draggable="true"
                      >
                        {be.name}
                      </button>
                    </div>
                  );
                })}
              </div>
            </div>

            <div className="action-area">
              <button
                id="frontend-btn"
                style={{ backgroundColor: 'rgba(98, 2, 238, 1)' }}
                draggable={!this.state.disabled}
                onDragStart={this.onFrontendDragStart}
                onDragEnd={this.onFrontendDragEnd}
              >
                <MoreVertIcon className="move-vert-icon" />
                <FrontendIcon className="middle-icon" />
                <span>Add a MicroFrontend</span>
              </button>
              <button
                id="backend-btn"
                style={{ backgroundColor: 'rgba(1, 163, 157, 1)' }}
                draggable={!this.state.disabled}
                onDragStart={this.onBackendDragStart}
                onDragEnd={this.onBackendDragEnd}
              >
                <MoreVertIcon className="move-vert-icon" />
                <BackendIcon className="middle-icon" />
                <span>Add a Backend Service</span>
              </button>
              <button
                id="hyperscaler-btn"
                style={{ backgroundColor: 'rgba(156, 39, 176, .8)', cursor: 'not-allowed' }}
              >
                <MoreVertIcon className="move-vert-icon" />
                <FunctionIcon className="middle-icon" />
                <span>Add a Serverless Function</span>
              </button>
              <button
                id="reuse-svc-btn"
                style={{ backgroundColor: 'rgba(3, 169, 244, 1)' }}
                draggable={!this.state.disabled}
                onDragStart={this.onBackendDragStart}
                onDragEnd={this.onBackendDragEnd}
              >
                <MoreVertIcon className="move-vert-icon" />
                <ReuseIcon className="middle-icon" />
                <span>Add a Reuse Service</span>
              </button>
              <button
                id="add-canvas-btn"
                style={{ backgroundColor: 'rgba(10, 110, 219, .8)', cursor: 'not-allowed' }}
              >
                <MoreVertIcon className="move-vert-icon" />
                <CanvasIcon className="middle-icon" />
                <span>Add a Canvas</span>
              </button>
            </div>
          </div>

          <div className="submit-area">
            <Button className="main-btn" variant="contained" color="primary" onClick={() => {}}>
              {'Save'}
            </Button>
            <Button
              className="normal-btn"
              variant="contained"
              onClick={() => {
                this.setState({
                  frontends: [],
                  backends: [],
                  feDragStarted: false,
                  beDragStarted: false,
                  feHighlight: false,
                  beHighlight: false,
                  hasBff: false,
                });
              }}
            >
              {'Reset'}     
            </Button>
          </div>

          <AddFrontendDialog
            serviceConfig={this.frontendConfig}
            open={this.state.addFeDialogOpen}
            handleClose={() => this.setState({ addFeDialogOpen: false })}
            handleSave={args => {
              this.setState(state => ({
                frontends: state.frontends.concat({
                  index: this.state.frontends.length + 1,
                  name: args.repository,
                  type: 'normal',
                }),
                addFeDialogOpen: false,
              }));
            }}
            dialogTitle={<Typography color="primary">Add a Micro Frontend</Typography>}
          />
          <AddServiceDialog
            serviceConfig={this.serviceConfig}
            open={this.state.addBeDialogOpen}
            handleClose={() => this.setState({ addBeDialogOpen: false })}
            handleSave={args => {
              this.setState(state => ({
                backends: state.backends.concat({
                  index: this.state.backends.length + 1,
                  name: args.repository,
                  type: 'normal',
                }),
                addBeDialogOpen: false,
              }));
            }}
            dialogTitle={<Typography color="primary">Add a Micro Service</Typography>}
          />
          <ReuseServiceDialog
            services={this.reuseServices}
            open={this.state.addReuseSvcDialogOpen}
            handleClose={() => this.setState({ addReuseSvcDialogOpen: false })}
            handleSave={() => {}}
            dialogTitle={<Typography color="primary">Add a Reuse Service</Typography>}
          />
        </section>
      </>
    );
  }
}

/* istanbul ignore next */
function mapStateToProps(state) {
  return {
    businessPartner: state.businessPartner,
  };
}

/* istanbul ignore next */
function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators({ ...actions }, dispatch),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(CanvasView);
